import { tab } from "@testing-library/user-event/dist/tab";
import { useState, useEffect } from "react";
import Table from "./Components/Table";
import "./App.css"

function App() {

  const [tables, setTables] = useState([])

  useEffect(()=>{
    addEmptyTable()
    
  },
  [])

  function addEmptyTable(){
    addTable("Name", "Pros", "", "Cons", "", "Other Considerations", "")
  }

  function addTable(name, c1Name, c2Name, c3Name, c1Contents, c2Contents, c3Contents){
    
    console.log("adding "+name)
    
    var tempArray = []
    tables.forEach(table=>{
      console.log("hello "+table.name)
      tempArray.push(table)
    })

    // Push a new table to the array
    tempArray.push(
      {name:name,
        tableContents:{
          c1Name:c1Name,
          c1Contents:c1Contents,
          c2Name:c2Name,
          c2Contents:c2Contents,
          c3Name:c3Name,
          c3Contents:c3Contents,
        }
      }
    )

    // Save it in state
    setTables(tempArray)
  }
  return (
    <div className="App">
      <div className="displaySection">
        <div className="addButton" onClick={addEmptyTable}>+</div>
        {tables.map(tableData=>(
          <Table          
            tableData = {tableData}
          ></Table>
        ))}
      </div>

    </div>
  );
}

export default App;
