import React from 'react'

function Table(props) {
    
    // Saves the data in state, saves state into cache
    function saveData(){
        document.getElementById("tableName")
    }

  return (
    <div className='table' >
        <div className='hoverMenuButton'>
            <div className='hoverMenu'>
                <div className='hoverMenuButtonInner'>Delete</div>
                <div className='hoverMenuButtonInner'>Duplicate</div>
            </div>
        </div>
        <input className='title' defaultValue={props.tableData.name} id="tableName"></input>
        <div className='column'>
            <input className='title titleSmaller' defaultValue={props.tableData.tableContents.c1Name}></input>
            <textarea defaultValue={props.tableData.tableContents.c1Contents}></textarea>
        </div>
        <div className='column'>
            <input className='title titleSmaller' defaultValue={props.tableData.tableContents.c1Name}></input>
            <textarea defaultValue={props.tableData.tableContents.c1Contents}></textarea>
        </div>
        <div className='column'>
            <input className='title titleSmaller' defaultValue={props.tableData.tableContents.c1Name}></input>
            <textarea defaultValue={props.tableData.tableContents.c1Contents}></textarea>
        </div>
    </div>
  )
}

export default Table